import React, {Component} from 'react';

export default class AddTask extends Component {
    render() {
        return(
            <div>
                <form className="add-task" onSubmit={this.props.addTask} >
                    <input type="text" placeholder="Enter Task" onChange={this.props.getTask} />
                    <input type="submit" value="Add Task" />
                </form>
            </div>
        )
    }
}