import React, {Component} from 'react';
import AddTask from './components/add-task';
import ItemsList from './components/items-list'
import './App.css';

export default class App extends Component {

  state = {
    itemsList: [],
    item: ''
  }

  getTask = (e) => {
    this.setState({
      item: e.target.value
    })
  }

  addTask = (e) => {
    e.preventDefault();

    if(e.target.elements[0].value !=='') {
        let itemsList = [...this.state.itemsList, {
          id: Math.random(),
          item: this.state.item
        }];
    
        this.setState({
          itemsList,
          item: ''
        });
    
        e.target.elements[0].value = '';
    }

    
  }
  
  deleteTask = (id) => {
    let itemsList = [...this.state.itemsList],
        newItemsList = itemsList.filter(item => item.id !== id);

    this.setState({
      itemsList: newItemsList
    })
  }

  render() {
    return(
      <div className="app">
        <AddTask getTask={this.getTask} addTask={this.addTask} />
        { this.state.itemsList.length === 0 ? <div className="main-item">There is no Tasks</div> : <ItemsList itemsList={this.state.itemsList} deleteTask={this.deleteTask} /> }
        
      </div>
    )
  } 
}

